export function createStore(storage) {
  return {
    add: function add(item) {
      storage.onRemove(item.id);
      storage.onAdd(item);
    },
    remove: function remove(id) {
      storage.onRemove(id);
    },
    getAll: function getAll(query) {
      return storage.getAll(query);
    }
  };
}